
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px white solid;
  
  } 
  
  .logo img {
    height: 40px;
    width: auto;
  }
  
  .buttons button {
    margin-left: 10px;
    padding: 8px 16px;
    background-color: #000;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 20px;
  }
  
  